<h1 id="jssdk" tabindex="-1"><a class="header-anchor" href="#jssdk" aria-hidden="true">#</a> JSSDK</h1>
<p>企业微信 JSSDK 官方文档：https://open.work.weixin.qq.com/api/doc/90000/90136/90514</p>
<h2 id="api" tabindex="-1"><a class="header-anchor" href="#api" aria-hidden="true">#</a> API</h2>
<h3 id="获取config接口配置" tabindex="-1"><a class="header-anchor" href="#获取config接口配置" aria-hidden="true">#</a> 获取config接口配置</h3>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">jssdk</span><span class="token operator">-></span><span class="token function">buildConfig</span><span class="token punctuation">(</span><span class="token keyword type-hint">array</span> <span class="token variable">$APIs</span><span class="token punctuation">,</span> <span class="token variable">$debug</span> <span class="token operator">=</span> <span class="token constant boolean">false</span><span class="token punctuation">,</span> <span class="token variable">$beta</span> <span class="token operator">=</span> <span class="token constant boolean">false</span><span class="token punctuation">,</span> <span class="token variable">$json</span> <span class="token operator">=</span> <span class="token constant boolean">true</span><span class="token punctuation">,</span> <span class="token keyword type-hint">array</span> <span class="token variable">$openTagList</span> <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br></div></div><p>默认返回 JSON 字符串，当 <code>$json</code> 为 <code>false</code> 时返回数组，你可以直接使用到网页中。</p>
<ul>
<li>设置当前URL</li>
</ul>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">jssdk</span><span class="token operator">-></span><span class="token function">setUrl</span><span class="token punctuation">(</span><span class="token variable">$url</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token variable">$app</span><span class="token operator">-></span><span class="token property">jssdk</span><span class="token operator">-></span><span class="token function">buildConfig</span><span class="token punctuation">(</span><span class="token keyword type-hint">array</span> <span class="token variable">$APIs</span><span class="token punctuation">,</span> <span class="token variable">$debug</span> <span class="token operator">=</span> <span class="token constant boolean">false</span><span class="token punctuation">,</span> <span class="token variable">$beta</span> <span class="token operator">=</span> <span class="token constant boolean">false</span><span class="token punctuation">,</span> <span class="token variable">$json</span> <span class="token operator">=</span> <span class="token constant boolean">true</span><span class="token punctuation">,</span> <span class="token keyword type-hint">array</span> <span class="token variable">$openTagList</span> <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token punctuation">]</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br></div></div><p>如果不想用默认读取的URL，可以使用此方法手动设置，通常不需要。</p>
<ul>
<li>示例</li>
</ul>
<p>我们可以生成js配置文件：</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token operator">&lt;</span>script src<span class="token operator">=</span><span class="token string">"https://res.wx.qq.com/open/js/jweixin-1.4.0.js"</span> type<span class="token operator">=</span><span class="token string">"text/javascript"</span> charset<span class="token operator">=</span><span class="token string">"utf-8"</span><span class="token operator">></span><span class="token operator">&lt;</span><span class="token operator">/</span>script<span class="token operator">></span>
<span class="token operator">&lt;</span>script type<span class="token operator">=</span><span class="token string">"text/javascript"</span> charset<span class="token operator">=</span><span class="token string">"utf-8"</span><span class="token operator">></span>
    wx<span class="token punctuation">.</span><span class="token function">config</span><span class="token punctuation">(</span><span class="token operator">&lt;</span><span class="token operator">?</span>php echo $app<span class="token operator">-</span><span class="token operator">></span>jssdk<span class="token operator">-</span><span class="token operator">></span><span class="token function">buildConfig</span><span class="token punctuation">(</span><span class="token function">array</span><span class="token punctuation">(</span><span class="token string">'updateAppMessageShareData'</span><span class="token punctuation">,</span> <span class="token string">'updateTimelineShareData'</span><span class="token punctuation">)</span><span class="token punctuation">,</span> <span class="token boolean">true</span><span class="token punctuation">)</span> <span class="token operator">?</span><span class="token operator">></span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token operator">&lt;</span><span class="token operator">/</span>script<span class="token operator">></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br></div></div><p>结果如下：</p>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token operator">&lt;</span>script src<span class="token operator">=</span><span class="token string">"https://res.wx.qq.com/open/js/jweixin-1.4.0.js"</span> type<span class="token operator">=</span><span class="token string">"text/javascript"</span> charset<span class="token operator">=</span><span class="token string">"utf-8"</span><span class="token operator">></span><span class="token operator">&lt;</span><span class="token operator">/</span>script<span class="token operator">></span>
<span class="token operator">&lt;</span>script type<span class="token operator">=</span><span class="token string">"text/javascript"</span> charset<span class="token operator">=</span><span class="token string">"utf-8"</span><span class="token operator">></span>
wx<span class="token punctuation">.</span><span class="token function">config</span><span class="token punctuation">(</span><span class="token punctuation">{</span>
    debug<span class="token operator">:</span> <span class="token boolean">true</span><span class="token punctuation">,</span> <span class="token comment">// 请在上线前删除它</span>
    appId<span class="token operator">:</span> <span class="token string">'wx3cf0f39249eb0e60'</span><span class="token punctuation">,</span>
    timestamp<span class="token operator">:</span> <span class="token number">1430009304</span><span class="token punctuation">,</span>
    nonceStr<span class="token operator">:</span> <span class="token string">'qey94m021ik'</span><span class="token punctuation">,</span>
    signature<span class="token operator">:</span> <span class="token string">'4F76593A4245644FAE4E1BC940F6422A0C3EC03E'</span><span class="token punctuation">,</span>
    jsApiList<span class="token operator">:</span> <span class="token punctuation">[</span><span class="token string">'updateAppMessageShareData'</span><span class="token punctuation">,</span> <span class="token string">'updateTimelineShareData'</span><span class="token punctuation">]</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token operator">&lt;</span><span class="token operator">/</span>script<span class="token operator">></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br></div></div><h3 id="获取agentconfig接口配置" tabindex="-1"><a class="header-anchor" href="#获取agentconfig接口配置" aria-hidden="true">#</a> 获取agentConfig接口配置</h3>
<p>调用wx.agentConfig之前，必须确保先成功调用wx.config. 注意：从企业微信3.0.24及以后版本（可通过企业微信UA判断版本号），无须先调用wx.config，可直接wx.agentConfig.</p>
<div class="language-php ext-php line-numbers-mode"><pre v-pre class="language-php"><code><span class="token variable">$app</span><span class="token operator">-></span><span class="token property">jssdk</span><span class="token operator">-></span><span class="token function">buildAgentConfig</span><span class="token punctuation">(</span>
        <span class="token keyword type-hint">array</span> <span class="token variable">$jsApiList</span><span class="token punctuation">,</span> <span class="token comment">// 需要检测的JS接口列表</span>
        <span class="token variable">$agentId</span><span class="token punctuation">,</span> <span class="token comment">//应用id</span>
        <span class="token keyword type-declaration">bool</span> <span class="token variable">$debug</span> <span class="token operator">=</span> <span class="token constant boolean">false</span><span class="token punctuation">,</span>
        <span class="token keyword type-hint">bool</span> <span class="token variable">$beta</span> <span class="token operator">=</span> <span class="token constant boolean">false</span><span class="token punctuation">,</span>
        <span class="token keyword type-hint">bool</span> <span class="token variable">$json</span> <span class="token operator">=</span> <span class="token constant boolean">true</span><span class="token punctuation">,</span>
        <span class="token keyword type-hint">array</span> <span class="token variable">$openTagList</span> <span class="token operator">=</span> <span class="token punctuation">[</span><span class="token punctuation">]</span><span class="token punctuation">,</span>
        <span class="token keyword type-hint">string</span> <span class="token variable">$url</span> <span class="token operator">=</span> <span class="token constant">null</span> <span class="token comment">//设置当前URL</span>
    <span class="token punctuation">)</span><span class="token punctuation">;</span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br></div></div><ul>
<li>前端示例</li>
</ul>
<div class="language-javascript ext-js line-numbers-mode"><pre v-pre class="language-javascript"><code><span class="token operator">&lt;</span>script src<span class="token operator">=</span><span class="token string">"https://res.wx.qq.com/open/js/jweixin-1.4.0.js"</span> type<span class="token operator">=</span><span class="token string">"text/javascript"</span> charset<span class="token operator">=</span><span class="token string">"utf-8"</span><span class="token operator">></span><span class="token operator">&lt;</span><span class="token operator">/</span>script<span class="token operator">></span>
<span class="token operator">&lt;</span>script src<span class="token operator">=</span><span class="token string">"https://open.work.weixin.qq.com/wwopen/js/jwxwork-1.0.0.js"</span><span class="token operator">></span><span class="token operator">&lt;</span><span class="token operator">/</span>script<span class="token operator">></span>
<span class="token operator">&lt;</span>script type<span class="token operator">=</span><span class="token string">"text/javascript"</span> charset<span class="token operator">=</span><span class="token string">"utf-8"</span><span class="token operator">></span>
wx<span class="token punctuation">.</span><span class="token function">config</span><span class="token punctuation">(</span><span class="token punctuation">{</span>
    debug<span class="token operator">:</span> <span class="token boolean">true</span><span class="token punctuation">,</span> <span class="token comment">// 请在上线前删除它</span>
    appId<span class="token operator">:</span> <span class="token string">'wx3cf0f39249eb0e60'</span><span class="token punctuation">,</span>
    timestamp<span class="token operator">:</span> <span class="token number">1430009304</span><span class="token punctuation">,</span>
    nonceStr<span class="token operator">:</span> <span class="token string">'qey94m021ik'</span><span class="token punctuation">,</span>
    signature<span class="token operator">:</span> <span class="token string">'4F76593A4245644FAE4E1BC940F6422A0C3EC03E'</span><span class="token punctuation">,</span>
    jsApiList<span class="token operator">:</span> <span class="token punctuation">[</span><span class="token string">'updateAppMessageShareData'</span><span class="token punctuation">,</span> <span class="token string">'updateTimelineShareData'</span><span class="token punctuation">]</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
wx<span class="token punctuation">.</span><span class="token function">ready</span><span class="token punctuation">(</span><span class="token keyword">function</span><span class="token punctuation">(</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    wx<span class="token punctuation">.</span><span class="token function">agentConfig</span><span class="token punctuation">(</span><span class="token punctuation">{</span> <span class="token comment">//调用agentConfig</span>
        corpid<span class="token operator">:</span> <span class="token string">''</span><span class="token punctuation">,</span> 
        agentid<span class="token operator">:</span> <span class="token string">''</span><span class="token punctuation">,</span> 
        timestamp<span class="token operator">:</span> <span class="token string">''</span><span class="token punctuation">,</span> 
        nonceStr<span class="token operator">:</span> <span class="token string">''</span><span class="token punctuation">,</span> 
        signature<span class="token operator">:</span> <span class="token string">''</span><span class="token punctuation">,</span>
        jsApiList<span class="token operator">:</span> <span class="token punctuation">[</span><span class="token string">'selectExternalContact'</span><span class="token punctuation">]</span><span class="token punctuation">,</span>
        <span class="token function-variable function">success</span><span class="token operator">:</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">res</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
            <span class="token comment">// 回调</span>
        <span class="token punctuation">}</span><span class="token punctuation">,</span>
        <span class="token function-variable function">fail</span><span class="token operator">:</span> <span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">res</span><span class="token punctuation">)</span> <span class="token punctuation">{</span>
            <span class="token keyword">if</span><span class="token punctuation">(</span>res<span class="token punctuation">.</span>errMsg<span class="token punctuation">.</span><span class="token function">indexOf</span><span class="token punctuation">(</span><span class="token string">'function not exist'</span><span class="token punctuation">)</span> <span class="token operator">></span> <span class="token operator">-</span><span class="token number">1</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
                <span class="token function">alert</span><span class="token punctuation">(</span><span class="token string">'版本过低请升级'</span><span class="token punctuation">)</span>
            <span class="token punctuation">}</span>
        <span class="token punctuation">}</span>
    <span class="token punctuation">}</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token punctuation">}</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
wx<span class="token punctuation">.</span><span class="token function">error</span><span class="token punctuation">(</span><span class="token keyword">function</span><span class="token punctuation">(</span><span class="token parameter">res</span><span class="token punctuation">)</span><span class="token punctuation">{</span>
    console<span class="token punctuation">.</span><span class="token function">log</span><span class="token punctuation">(</span><span class="token string">'失败'</span><span class="token punctuation">)</span><span class="token punctuation">;</span>  
<span class="token punctuation">}</span><span class="token punctuation">)</span><span class="token punctuation">;</span>
<span class="token operator">&lt;</span><span class="token operator">/</span>script<span class="token operator">></span>
</code></pre><div class="line-numbers"><span class="line-number">1</span><br><span class="line-number">2</span><br><span class="line-number">3</span><br><span class="line-number">4</span><br><span class="line-number">5</span><br><span class="line-number">6</span><br><span class="line-number">7</span><br><span class="line-number">8</span><br><span class="line-number">9</span><br><span class="line-number">10</span><br><span class="line-number">11</span><br><span class="line-number">12</span><br><span class="line-number">13</span><br><span class="line-number">14</span><br><span class="line-number">15</span><br><span class="line-number">16</span><br><span class="line-number">17</span><br><span class="line-number">18</span><br><span class="line-number">19</span><br><span class="line-number">20</span><br><span class="line-number">21</span><br><span class="line-number">22</span><br><span class="line-number">23</span><br><span class="line-number">24</span><br><span class="line-number">25</span><br><span class="line-number">26</span><br><span class="line-number">27</span><br><span class="line-number">28</span><br><span class="line-number">29</span><br><span class="line-number">30</span><br><span class="line-number">31</span><br><span class="line-number">32</span><br><span class="line-number">33</span><br></div></div>